export default {
	data() {
		return {
			
		};
	},
	methods:{
		wxGetUserInfo:function(res){
			console.log(res)
			if (!res.detail.iv) {
				uni.showToast({
					title: "您取消了授权,登录失败",
					icon: "none"
				});
				return false;
			}
			console.log('-------用户授权，并获取用户基本信息和加密数据------')
			// console.log(res.detail);
		},
		wxLogin: function() {
			uni.login({
				provider: 'weixin',
				success: function(loginRes) {
					console.log('-------获取code-------')
					console.log(loginRes.code);
					wx.request({
						url: 'https://xxxxx'+loginRes.code,
						success: function(info) {
							console.log('-------获取sessionKey、openid(unionid)-------')
							console.log(info);
						},
						fail: function(e) {
							console.log(e)
						}
					})
				}
			});
		}
	}
}
